.decrypto-game {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Roboto', sans-serif;
}

.game-boards {
  display: flex;
  flex-direction: row;
  gap: 20px;
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  .game-boards {
    flex-direction: column;
  }
}

.decrypto-game h1, .decrypto-game h2, .decrypto-game h3 {
  color: #333;
  margin-bottom: 15px;
}

.decrypto-game button {
  cursor: pointer;
  padding: 8px 16px;
  border-radius: 4px;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.decrypto-game button:hover {
  opacity: 0.9;
}

.decrypto-game label {
  font-weight: bold;
  margin-bottom: 5px;
  display: block;
}

.decrypto-game input, .decrypto-game select {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  width: 100%;
}

.decrypto-game input:focus, .decrypto-game select:focus {
  outline: none;
  border-color: #007bff;
} 